/* CSSTerm.com Simple CSS menu */

.menu_simple ul {
  margin: 0;
  padding: 0;
  width: 100%;
  list-style-type: none;
  background-color: #2460B9;
  li {
    line-height: 200%;
  }
}

.menu_simple ul li {
  border-bottom: 1px dotted #fff;
  a {
    text-decoration: none;
    color: white;
    padding: 5.5px 11px;
    display: block;
  }
}

.menu_simple ul li a:visited {
  color: white;
}

.menu_simple ul li a:hover, .menu_simple ul li .current {
  color: white;
  background-color: #4FC2E5;
}

#featured_slide {
  display: block;
  width: 100%;
  height: 385px;
  padding: 0;
  overflow: hidden;
  font-family: Georgia, "Times New Roman", Times, serif;
}

#featured_slide a {
  color: #FFFFFF;
  background-color: transparent;
}

#featured_slide a, #featured_slide ul, #featured_slide img {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  list-style: none;
  text-decoration: none;
}

#featured_slide ul {
  margin: 15px 0 0 0;
}

#featured_slide h1, #featured_slide h2, #featured_slide h3, #featured_slide h4, #featured_slide h5, #featured_slide h6, #featured_slide p {
  margin: 0 0 4px 0;
  padding: 0;
  line-height: normal;
  font-size: 20px;
  font-weight: normal;
}

#featured_slide a {
  color: #059AD8;
  background-color: transparent;
  font-weight: normal;
}

#featured_slide p {
  margin: 0;
  font-size: 14px;
}

.gallery {
  color: #000;
  padding: 15px;
}

.loader {
  background: url("../scripts/galleryviewthemes/loader.gif") center center no-repeat;
}

.panel .panel-overlay, .panel .overlay-background {
  height: 60px;
  padding: 10px;
}

.panel .overlay-background {
  background: #222;
}

.panel .panel-overlay {
  color: white;
  font-size: 0.7em;
}

.panel .panel-overlay a {
  color: white;
  text-decoration: underline;
  font-weight: bold;
}

.filmstrip {
  margin: 15px 0 0 0;
  padding: 0;
}

.filmstrip .gv_thumbnail {
  margin: 0;
  padding: 0;
}

.gv_frame {
  float: left;
  cursor: pointer;
  position: relative;
}

.gv_frame .gv_thumbnail {
  border: 1px solid #aaa;
}

.gv_frame.current .img_wrap {
  border-color: #000;
}

.gv_frame img {
  border: none;
}

.gv_frame .caption {
  text-align: center;
  color: #888;
}

.gv_frame.current .caption {
  color: #000;
}

.pointer {
  border-color: #000;
}

#featured_slide img {
  width: 100% !important;
  position: relative !important;
  left: 0px !important
}

.gv_panel img {
  height: 280px !important;
  top: 0px !important
}

/* latest news */

.latestnews {
