  width: 100%;
  margin: 5px;
  padding: 10px;
  border-bottom: 1px dotted #C7C5C8;
  overflow: hidden;
}

.latestnews li.last {
  margin-bottom: 0;
}

.latestnews p {
  display: inline;
}

.latestnews img {
  float: left;
  margin: 0 10px 0 0;
  padding: 4px;
  border: 1px solid #C7C5C8;
  clear: left;
}

.first-row {
  div {
    margin-left: 0px;
  }
}

/****************** Marquee **************************/

.container {
  background: #4FC2E5;
  overflow: hidden;
  box-sizing: border-box;
  height: 25px;
  cursor: pointer;
}

.marquee-sibling {
  padding: 5px;
  background: #2460B9;
  width: 20%;
  height: 45px;
  font-size: 12px;
  font-weight: normal;
  color: #ffffff;
  text-align: center;
  float: left;
  left: 0;
  z-index: 2000;
}

.marquee,
*[class^="marquee"] {
  display: inline-block;
  white-space: nowrap;
  position: absolute;
}

.marquee {
  margin-left: 25%;
}

.marquee-content-items {
  display: inline-block;
  padding: 5px;
  margin: 0;
  height: 45px;
  position: relative;
}

.marquee-content-items li {
  display: inline-block;
  color: #fff;
}

.marquee-content-items li:after {
  content: "|";
  margin: 0 1em;
}

.website-banner-content {

  h2 {
    font-size: 25px;
    margin: 10px;
  }
}
.top-menu .nav{
  margin-left: 10%;
}
.nav {

  border-bottom: 1px solid #c32626;
  .active {
    background-color: #c32626 !important;
    border-radius: 5px 5px 0px 0px;
    box-shadow: 2px 2px 5px #888888;
    line-height: 40px;
    a {
      background-color: transparent !important;
      color: #fff !important;
      border-color: transparent !important;
    }
  }
}

